#Total-land2-div{
    display:flex;
    align-items: center;
}

.LPC{
    border: 2px solid black;
    margin-left: 2vw;
    margin-right: 2vw;
    font-family: 'Corben';  
    display: flex;
    justify-content: center;
    align-items: center;
}
#LPC1{
    height: 20vw;
    width: 45vw;
    font-size: 3vw;
        color: white;
        /* height: 7vw;
        width: 40vw; */
        /* border: 10px solid white; */
        text-align: center;
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
        background-color: #361eb0;

}
#LPC2{
    height: 35vw;
    width: 60vw;
    display: inline-block;
    margin: 0 auto;
    
}
.img{
    width: 100%;
    height: 100%
}
.char{
    transform:translateY(115px);
    transition:transfrom .5s;
  }
@media screen and (max-width:650px) {
    #Total-land2-div{
        display: flex;
        /* border: 10px solid pink; */
        flex-direction: column-reverse;
    }
    #LPC2{
        width: 85vw;
        height: 35vh;
        margin-top: 4vh;
    }
    #LPC1{
        width: 80vw;
        border-radius: 5vw;
        font-size: 4vw;
    }
    
}
@media screen and (max-width:450px) {
    #LPC2{
    
        height: 28vh;
    }
    #LPC1{
        width: 80vw;
        border-radius: 5vw;
        font-size: 4.7vw;
        height: 20vh;
    }
    
}


  